<template>
  <v-app>
    <!-- Splash screen -->
    <div v-if="splashScreen" id='GladosMainSplashScreen' class="loader-background">
        <div class="card splash-card z-depth-0">
        <div class="loader-container">
            <div class="spinner">
                <div class="ball ball-1"></div>
                <div class="ball ball-2"></div>
                <div class="ball ball-3"></div>
                <div class="ball ball-4"></div>
                <div class="ball ball-5"></div>
                <div class="ball ball-6"></div>
            </div>
            <span class="chembl-logo center-align"> ChEMBL </span>
            <span class="center-align loading-msg"> Loading... </span>
        </div>
        </div>
    </div>    
    <v-content>
      <router-view/>
    </v-content>
    <v-footer :fixed="fixed" app>
      <v-flex
        text-md-center
      >
        &copy;2018 — <strong>Chembl</strong>
      </v-flex>
    </v-footer>
  </v-app>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  data () {
    return {
      splashScreen: false,
      clipped: false,
      drawer: true,
      fixed: false,
      items: [
        {
          icon: 'home',
          title: 'Home',
          path: '/'
        },
        {
          icon: 'low_priority',
          title: 'Test',
          path: '/test'
        }
      ],
      miniVariant: true,
      right: false,
      rightDrawer: false,
      title: 'xs-protein-search'
    }
  },
  components: {
    HelloWorld
  },
  temp_proteins: [],
  name: 'App'
}
</script>


